<script setup>
import { useStorage } from "vue3-storage";

const storage = useStorage();

const randomData = storage.getStorageSync("randomData");
</script>
<template>
  <section
    id="randomWinner"
    :style="
      randomData.selectBg === 0 ? `background: ${randomData.bgColor}` : ''
    "
  >
    <img
      v-if="randomData.selectBg === 1"
      :class="randomData.isVertical ? 'vertical' : 'horizontal'"
      :src="randomData.bgSrc"
      :alt="randomData.title"
    />
    <article class="content" :style="`color:${randomData.fontColor}`">
      <p class="logo font-sans-serif">Lucky Draw</p>
      <div class="title">
        <h2
          :class="
            randomData.selectFont === 0
              ? 'font-sans-serif'
              : randomData.selectFont === 1
              ? 'font-serif'
              : 'font-calligraphic'
          "
        >
          {{ randomData.title }}
        </h2>
        <p>{{ randomData.detail }}</p>
      </div>
      <ul :class="randomData.winner.length > 5 ? 'all' : ''">
        <li
          v-for="(winner, wIdx) in randomData.winner"
          :key="wIdx"
          :style="
            randomData.winner.length > 5
              ? `border: 1px solid ${randomData.fontColor}`
              : ''
          "
        >
          {{ winner }}
        </li>
      </ul>
    </article>
  </section>
</template>
